import React from 'react';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

export const NumberOfCardsDropDown = (props) => {
    const [numberOfCards, setNumberOfCards] = React.useState('');
    const setRow = props.setrow;

    const handleChange = event => {
        setNumberOfCards(event.target.value);
        // console.log(event.target.value);
        setRow(event.target.value);
    };

    var values = [1,2,3,4,5,6];

    return (
        <FormControl variant="outlined" fullWidth >
            <Select
                value={numberOfCards}
                onChange={handleChange}
                fullWidth
            >
                {values.map(item =>
                    <MenuItem value={values.indexOf(item)+1} key={item}>
                        <div style={{ fontSize: 30 }}> 
                            {item}
                        </div>
                    </MenuItem>
                )}
            </Select>
        </FormControl>
    )
}

export const SuitDropDown = () => {
    const [suit, setSuit] = React.useState('');

    const handleChange = event => {
        setSuit(event.target.value);
    };

    var values = ['♠', '♥', '♣', '♦'];

    return (
        <FormControl variant="outlined" fullWidth >
            <Select
                value={suit}
                onChange={handleChange}
                fullWidth
            >
                {values.map(item =>
                    values.indexOf(item)%2==0 ?
                    <MenuItem value={values.indexOf(item)} key={item}>
                        <div style={{ fontSize: 30 }}> 
                        {item}
                        </div>
                    </MenuItem>
                    :
                    <MenuItem value={values.indexOf(item)} key={item}>
                        <div style={{ color: 'red', fontSize: 30 }}> 
                        {item}
                        </div>
                    </MenuItem>
                )}
                
            </Select>
        </FormControl>
    )
}

export const CardValueDropDown = () => {
    const [cardValue, setCardValue] = React.useState('');

    const handleChange = event => {
        setCardValue(event.target.value);
    };

    var values = ['A','K','10','5','Q','J','2','3','4','6','7','8','9']

    return (
        <FormControl variant="outlined" fullWidth >
            <Select
                value={cardValue}
                onChange={handleChange}
                fullWidth
            >
                {values.map(item =>
                    <MenuItem value={values.indexOf(item)} key={item}>
                        <div style={{ fontSize: 30 }}> 
                            {item}
                        </div>
                    </MenuItem>
                )}

            </Select>
        </FormControl>
    )
}

